/* src/styles/NavBar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    padding: 16px;
    background-color: #1da1f2;
    color: #fff;
  }
  
  .nav-links a {
    margin-left: 15px;
    color: #fff;
    text-decoration: none;
  }
  
  .nav-links a:hover {
    text-decoration: underline;
  }
  