/* src/styles/Post.css */
.post-container {
    border: 1px solid #e1e8ed;
    border-radius: 10px;
    padding: 16px;
    background-color: #fff;
    margin-bottom: 15px;
    max-width: 600px;
  }
  
  .post-header {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .post-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
  }
  