/* src/styles/Home.css */

.home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: #f5f8fa;
  }
  
  .post-container {
    width: 100%;
    max-width: 600px;
    margin-bottom: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
    background-color: #fff;
    overflow: hidden;
    transition: transform 0.2s ease;
  }
  
  .post-container:hover {
    transform: translateY(-3px);
  }
  