/* src/styles/About.css */
.about-container {
    padding: 20px;
    text-align: center;
}

.about-title {
    font-size: 2rem;
    font-weight: bold;
    color: #1DA1F2;
    margin-bottom: 10px;
}

.about-description {
    font-size: 1.2rem;
    color: #333;
    line-height: 1.6;
    max-width: 600px;
    margin: 0 auto;
}

.about-image {
    width: 100%;
    max-width: 300px;
    margin-top: 20px;
    border-radius: 8px;
}
